@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,600;1,100;1,200;1,300&display=swap');

body {
  margin: 0;
}

.App {
  display: flex; 
  flex-direction: row;
  background-color: #0A0A0A;
  justify-content: center; 
  background: linear-gradient(#009dff, #30D5C8);
  height: 100vh;
}

h1 {
  font-family: 'Raleway', sans-serif; 
  font-weight: 600; 
  color: white; 
  font-size: 48px; 
  text-align: center; 
  margin-bottom: 10px; 
  margin-top: 0px; 
}


#controlPanel {
  width: 30%; 
  height: 100vh; 
  display: flex; 
  flex-direction: column; 
  justify-content: center; 
  align-items: center; 
  margin-left: 3%;
}

.barContainer {
  width: 55%; 
  height: 88vh; 
  margin-left: auto; 
  margin-right: auto; 
  display: flex; 
  flex-direction: row; 
  justify-content: space-between; 
  align-items: flex-end; 
}

#controls {
  width: 100%;
  display: flex; 
  flex-direction: row; 
  flex-wrap: wrap; 
  justify-content: space-evenly; 
  align-items: center; 
  margin-top: 5%; 
}

button {
  background-color: white;
  color: black; 
  border-radius: 20px; 
  width: 34%; 
  padding-top: 10px;
  padding-bottom: 10px; 
  transition-duration: 0.5s; 
  font-family: 'Raleway', sans-serif; 
  font-weight: 600; 
  font-size: 16px; 
  margin-top: 20px; 
  margin-bottom: 20px; 
  border: 0px; 
  opacity: 0.5s; 
}

.clickable {
  opacity: 1; 
}

.clickable:hover {
  background-color: black; 
  color: white; 
  box-shadow: 0px 0px 12px 1px rgba(0, 0, 0, 0.4);
  transition-property: all; 
  transition-duration: 0.5s; 
  cursor: pointer; 
}

.bar {
  width: 4px; 
  background-color: #ECECEC;
  border-radius: 5px; 
}

.sliderContainer {
  width: 100%; 
  display: flex; 
  flex-direction: column; 
  justify-content: space-between; 
  align-items: center; 
  padding-top: 10px;
}

input::-webkit-slider-thumb {
  appearance: none; 
  -webkit-appearance: none; 
  background: black; 
  height: 15px; 
  width: 15px; 
  cursor: pointer; 
}

label {
  font-family: 'Raleway', sans-serif; 
  font-size: 16px; 
  font-weight: 500; 
  padding-top: 10px; 
  color: white;
}

input:disabled {
  opacity: 0.5; 
}

@media screen and (max-width: 1200px) {
  button {
    width: 45%;
  }

  input {
    width: 90%;
  }
}


@media screen and (max-width: 900px) {
  h1 {
    font-size: 48px;
    margin-top: 10px;
    padding-top: 0px;
    margin-bottom: 5px;
  }

  button {
    width: 100%;
    padding-top: 4px;
    padding-bottom: 4px;
    margin-top: 2%;
    margin-bottom: 2%;
    font-size: 14px;
  }

  #controls {
    height: 75%;
  }

  .bar {
    width: 2px;
  }

  #controlPanel {
    justify-content: center;
  }

}

@media screen and (max-width: 550px) {
  #controlPanel {
    margin-left: 30px;
    justify-content: space-evenly;
  }
  
  h1 {
    font-size: 40px;
  }
}

@media screen and (max-width: 450px) {
  h1 {
    font-size: 32px;
  }

  .barContainer {
    width: 50%;
  }

  #controls {
    height: 80%;
  }
}

@media screen and (max-height: 600px) and (min-width: 900px) {
  button {
    margin-top: 2%;
    margin-bottom: 2%;
  }
}

@media screen and (max-height: 600px) and (max-width: 900px) {
  h1 {
    font-size: 36px;
  }
}

@media screen and (max-height: 500px) {
  button {
    margin-top: 1%;
    margin-bottom: 1%;
  }

  h1 {
    font-size: 28px;
  }

  #controlPanel {
    justify-content: center;
  }
}

@media screen and (max-width: 900px) and (max-height: 400px) {
  button {
    width: 40%;
    margin-top: 1%;
    margin-bottom: 1%;
  }
}